"use client"
import React from "react"

import { useState } from "react"
import { useAuth } from "@/context/auth-context"
import ItemForm from "./item-form"
import ItemCard from "./item-card"

export default function ItemsGrid() {
    // State
    const [fade, setFade] = useState(true)
    const { items, deleteItem } = useAuth()
    const [showForm, setShowForm] = useState(false)
    const [filter, setFilter] = useState("semua")
    const [search, setSearch] = useState("")
    const [viewType, setViewType] = useState<'card' | 'table'>('card')

    // Filter status berdasarkan statusMesin
    let filteredItems = filter === "semua"
      ? items
      : items.filter((item) => item.statusMesin === filter)
    // Filter pencarian nama
    if (search.trim() !== "") {
      filteredItems = filteredItems.filter((item) =>
        item.name.toLowerCase().includes(search.trim().toLowerCase())
      )
    }

    // Handle transisi saat viewType berubah
    React.useEffect(() => {
      setFade(false)
      const timeout = setTimeout(() => setFade(true), 100)
      return () => clearTimeout(timeout)
    }, [viewType, filteredItems.length])

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">Item Terdaftar</h1>
          <p className="text-muted-foreground">Total {items.length} item dalam sistem</p>
        </div>
        <div className="flex gap-2 items-center">
          <button
            onClick={() => setViewType('card')}
            className={`px-4 py-2 rounded-lg font-semibold text-sm border ${viewType === 'card' ? 'bg-primary text-primary-foreground' : 'bg-white text-foreground border-border hover:bg-muted'}`}
          >
            Tampilan Card
          </button>
          <button
            onClick={() => setViewType('table')}
            className={`px-4 py-2 rounded-lg font-semibold text-sm border ${viewType === 'table' ? 'bg-primary text-primary-foreground' : 'bg-white text-foreground border-border hover:bg-muted'}`}
          >
            Tampilan Tabel
          </button>
          <button
            onClick={() => setShowForm(!showForm)}
            className="bg-primary text-primary-foreground px-6 py-3 rounded-lg hover:bg-primary/90 transition-colors font-semibold"
          >
            {showForm ? "Tutup Form" : "Tambah Item"}
          </button>
        </div>
      </div>

      {showForm && <ItemForm onClose={() => setShowForm(false)} />}

      <div className="flex flex-col md:flex-row md:items-center gap-3 flex-wrap">
        <input
          type="text"
          placeholder="Cari nama item..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="px-4 py-2 rounded-lg border border-border bg-background text-foreground focus:outline-none focus:ring-2 focus:ring-primary min-w-[200px]"
        />
        {[
          { label: "Semua", value: "semua" },
          { label: "Normal", value: "Normal" },
          { label: "Maintenance", value: "Maintenance" },
          { label: "Rusak", value: "Rusak" },
          { label: "Tidak Aktif", value: "Tidak Aktif" },
          { label: "Standby", value: "Standby" },
        ].map((btn) => (
          <button
            key={btn.value}
            onClick={() => setFilter(btn.value)}
            className={`px-4 py-2 rounded-lg transition-colors ${
              filter === btn.value
                ? "bg-primary text-primary-foreground"
                : "bg-white text-foreground border border-border hover:bg-muted"
            }`}
          >
            {btn.label}
          </button>
        ))}
      </div>

      {filteredItems.length === 0 ? (
        <div className="text-center py-12 bg-white rounded-xl border border-border">
          <p className="text-muted-foreground">Tidak ada item ditemukan</p>
        </div>
      ) : viewType === 'card' ? (
        <div className={`grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6`}>
          {filteredItems.map((item, idx) => (
            <div
              key={item.id}
              style={{
                transitionDelay: fade ? `${idx * 100}ms` : '0ms',
                transitionProperty: 'transform, opacity',
                transitionDuration: '700ms',
                transitionTimingFunction: 'cubic-bezier(0.22, 1, 0.36, 1)',
                transform: fade ? 'translateY(0)' : 'translateY(40px)',
                opacity: fade ? 1 : 0,
                boxShadow: fade ? '0 8px 32px rgba(80,80,180,0.10)' : 'none',
                willChange: 'transform, opacity',
              }}
              className="rounded-xl bg-white border border-border p-0"
            >
              <ItemCard item={item} onDelete={() => deleteItem(item.id)} />
            </div>
          ))}
        </div>
      ) : (
        <div className={`overflow-x-auto transition-all duration-500 ${fade ? 'opacity-100' : 'opacity-0'}`}>
          <table className="min-w-full bg-white rounded-xl border border-border">
            <thead>
              <tr className="bg-muted">
                <th className="px-4 py-2 text-left text-sm font-semibold">Nama</th>
                <th className="px-4 py-2 text-left text-sm font-semibold">Kategori</th>
                <th className="px-4 py-2 text-left text-sm font-semibold">Status Mesin</th>
                <th className="px-4 py-2 text-left text-sm font-semibold">Kuantitas</th>
                <th className="px-4 py-2 text-left text-sm font-semibold">Lokasi</th>
                <th className="px-4 py-2 text-left text-sm font-semibold">Aksi</th>
              </tr>
            </thead>
            <tbody>
              {filteredItems.map((item, idx) => (
                <tr
                  key={item.id}
                  style={{
                    transitionDelay: fade ? `${idx * 100}ms` : '0ms',
                    transitionProperty: 'transform, opacity',
                    transitionDuration: '700ms',
                    transitionTimingFunction: 'cubic-bezier(0.22, 1, 0.36, 1)',
                    transform: fade ? 'translateY(0)' : 'translateY(40px)',
                    opacity: fade ? 1 : 0,
                    willChange: 'transform, opacity',
                  }}
                  className="transition-all rounded-xl"
                >
                  <td className="px-4 py-2 text-sm font-semibold text-foreground">{item.name}</td>
                  <td className="px-4 py-2 text-sm text-muted-foreground">{item.category}</td>
                  <td className="px-4 py-2 text-sm">
                    <span className={`px-2 py-1 rounded text-xs font-semibold ${
                      item.statusMesin === "Normal" ? "bg-green-100 text-green-700" :
                      item.statusMesin === "Maintenance" ? "bg-yellow-100 text-yellow-700" :
                      item.statusMesin === "Rusak" ? "bg-red-100 text-red-700" :
                      item.statusMesin === "Standby" ? "bg-blue-100 text-blue-700" :
                      item.statusMesin === "Tidak Aktif" ? "bg-gray-100 text-gray-700" :
                      "bg-gray-100 text-gray-700"
                    }`}>
                      {item.statusMesin || "-"}
                    </span>
                  </td>
                  <td className="px-4 py-2 text-sm text-foreground">{item.quantity}</td>
                  <td className="px-4 py-2 text-sm text-foreground">{item.location}</td>
                  <td className="px-4 py-2 text-sm">
                    <button
                      onClick={() => deleteItem(item.id)}
                      className="px-3 py-1 bg-red-50 text-red-600 rounded hover:bg-red-100 text-xs font-semibold"
                    >
                      Hapus
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  )
}
